import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import TitleCard from "../../../components/Cards/TitleCard"
import { openModal } from "../../common/modalSlice"
import { CONFIRMATION_MODAL_CLOSE_TYPES, MODAL_BODY_TYPES } from '../../../utils/globalConstantUtil'
import { CheckBadgeIcon, EyeIcon, TrashIcon } from '@heroicons/react/24/outline'
import { WhatsApp } from "@mui/icons-material";
import { fetchMemberRegistrationAction } from "../../../app/service/action/memberAction";
import Loading from "../../../components/Loading/Loading";
import { Link } from "react-router-dom";
import { PaginationComponent } from "../../../components/Pagination/PaginationComponent";
import { StatusMember } from "../../../components/Typography/StatusMember";
import SearchBar from "../../../components/Input/SearchBar";
import { sendToWA } from "../../../utils/Utils"

const MemberRegistration = () => {
    const dispatch = useDispatch()
    const [page, setPage] = useState(1);
    const [search, setSearch] = useState('');
    const dataMember = useSelector(({ memberRegistrationSate }) => memberRegistrationSate.memberRegistration.data)
    const isLoading = useSelector(({ memberRegistrationSate }) => memberRegistrationSate.memberRegistration.isLoading)

    useEffect(() => {
        dispatch(fetchMemberRegistrationAction(page, search)).then().catch(error => {
            console.log(error)
        })
    }, [dispatch, page, search])

    const deleteCurrentMember = (index) => {
        dispatch(openModal({
            title: "Confirmation", bodyType: MODAL_BODY_TYPES.CONFIRMATION,
            extraObject: {
                message: `Apakah anda yakin akan menghapus member ini?`,
                type: CONFIRMATION_MODAL_CLOSE_TYPES.MEMBER_DELETE,
                index
            }
        }))
    }

    const handleChange = (_event, value) => {
        setPage(value);
        // @ts-ignore
        dispatch(fetchMemberRegistrationAction(value, search)).then(r => console.log(r));
    };

    return (
        <>
            <TitleCard title="Registration" topMargin="mt-2"
                TopSideButtons={<SearchBar placeholderText={'Cari Nama Rider'}
                    setSearchText={(input) => setSearch(input)} />}>
                {isLoading ? <Loading /> :
                    <div className="overflow-x-auto w-full">
                        <table className="table w-full">
                            <thead>
                                <tr>
                                    <th>Tanggal</th>
                                    <th>Data Rider</th>
                                    <th>Email</th>
                                    <th>Data Orang Tua</th>
                                    <th>Alamat</th>
                                    <th>Status</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    dataMember.result ? dataMember.result.map((value, k) => {
                                        return (
                                            <tr key={k}>
                                                <td>{value.createdAt}</td>
                                                <td>
                                                    <div className="flex items-center space-x-3">
                                                        <div>
                                                            <div className="font-bold">{value.fullname}</div>
                                                            <div className="text-sm opacity-50">{value.nickname}</div>
                                                            <div className="text-sm opacity-50">{value.categoryRace}</div>
                                                            <div className="text-sm opacity-50">{value.memberId}</div>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td>{value.email}</td>
                                                <td>
                                                    <div
                                                        className="text-sm">
                                                        Ayah: {value.fatherName} <br />
                                                        HP:
                                                        <a href={sendToWA(value.fatherPhone)} target="_blank" rel="noreferrer">
                                                            <WhatsApp className="w-5" color="green" /> {value.fatherPhone}</a>
                                                    </div>
                                                    <div className="text-sm">  Ibu: {value.motherName} <br />
                                                        HP:
                                                        <a href={sendToWA(value.motherPhone)} target="_blank" rel="noreferrer">
                                                            <WhatsApp className="w-5" color="green" /> {value.motherPhone} </a>
                                                    </div>
                                                </td>
                                                <td>{value.address}</td>
                                                <td>{StatusMember(value.status)}</td>
                                                <td>
                                                    <Link to={`${value.id}/verification`}>
                                                        <button className="btn btn-square btn-ghost">
                                                            <CheckBadgeIcon className="w-5" color="green" />
                                                        </button>
                                                    </Link>
                                                    <Link to={`${value.id}/update`}>
                                                        <button className="btn btn-square btn-ghost">
                                                            <EyeIcon className="w-5" color={'blue'} />
                                                        </button>
                                                    </Link>
                                                    <button className="btn btn-square btn-ghost"
                                                        onClick={() => deleteCurrentMember(value.id)}>
                                                        <TrashIcon className="w-5" color={'red'} />
                                                    </button>
                                                </td>
                                            </tr>
                                        )
                                    }) : (
                                        <tr>
                                            <td colSpan={7} align={'center'}>DATA TIDAK DITEMUKAN</td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>
                        <PaginationComponent data={dataMember} handleChange={handleChange} page={page} />
                    </div>
                }
            </TitleCard>
        </>
    )
}


export default MemberRegistration
